'use strict';

import React from 'react';
import { Link, IndexLink } from 'react-router'

let query='date';

class App extends React.Component {

    // App  

    componentWillMount () {
        console.log('App  ')
    }

    componentDidMount () {
        console.log('App  ')
    }

    componentWillReceiveProps () {
        console.log('App   props')
    }

    componentWillUpdate () {
        console.log('App  ')
    }

    componentDidUpdate () {
        console.log('App  ')
    }

    render() {
        return (
            <div>
                {/*<Link to="/"></Link>*/}
                {/*<Link to="/" activeClassName="active" onlyActiveOnIndex={true}>Home</Link>*/}
                <IndexLink to="/" activeStyle={{color: 'red'}}></IndexLink>
                <strong> - | - </strong>
                <Link to={`/tv?orderBy=${query}`} activeStyle={{color: 'red'}}>TV</Link>
                <strong> - | - </strong>
                <Link to="/movies" activeStyle={{color: 'red'}}>Movies</Link>
                <strong> - | - </strong>
                <Link to="/welcome" activeStyle={{color: 'red'}}>Welcome</Link>
                <hr/>

                {/*this.props.children  IndexRoute */}
                {this.props.children}
            </div>
        )
    }
}

export { App as default }


